.form-group-label {
    font-size: 1.125rem;
    margin-bottom: 1rem;
}

.form-control {
    font-size: 0.8125rem;
    height: 2.75rem;
    padding: 0.375rem 1rem;
    color: #000000;
    @include border-radius(0);
    @include shadow(none !important);

    &:hover {
    }

    &:focus {
    }
}

.form-group {
    position: relative;
    margin-bottom: 1.375rem;

    &.has--icon {
        .form-control {
            padding-right: 2rem;
        }
    }
}

textarea.form-control {
    min-height: 7.5rem;
}


.single-line-form {
    position: relative;
    max-width: 23.76rem;

    .form__input {
        background: #fff;
        width: 100%;
        border: 1px solid $color1;
        padding: 0.2rem 40% 0.2rem 1rem;

        @include extra-small() {
            padding-right: 25%;
        }
    }

    .form__submit {
        background-color: $color1;
        color: #fff;
        font-size: 1rem;
        text-transform: uppercase;
        text-align: center;
        width: 37.6%;
        position: absolute;
        right: 0;
        top: 0;
        border: none;
        z-index: 2;
        cursor: pointer;

        @include extra-small() {
            width: 20%;
        }

    }

    .form__submit,
    .form__input {
        height: 2.75rem;

        @include small(){
            height: 3rem;
        }
    }

}
