.user-quote {
    max-width: 20rem;
    
    .ratings-block {
        margin-bottom: 1.8rem;
    }
}


.user-quote__img {
    margin: 0 auto 1rem;
    @include sphere(16.25rem);
    @include medium() {
        @include square(10rem);
    }
    img {
        object-fit: cover;
        background-color: #ccc;
        @include square(100%);
    }
}