.font-weight-medium {
    font-weight: 500 !important;
}

.bg-black {
    background: #000000 !important;
}

.text-color {
    color: $text-color !important;
}

.text-primary {
    color: $color1 !important;
    &:hover {
        color: $color1 !important;
    }
}

.text-secondary {
    color: $color2 !important;
}

.text-transform-none {
    text-transform: none !important;
}

.text-underline-hover {
    &:hover {
        text-decoration: underline;
    }
}

.auto-width {
    width: auto !important;
}

.cursor-pointer {
    cursor: pointer;
}

.pointer-events-none {
    pointer-events: none;
}

.flex-centered {
    display: flex;
    justify-content: center;
    align-items: center;
}

.full-width {
    width: 100% !important;
}

.full-height {
    height: 100% !important;
}

.no-bg {
    background: none !important;
}

.filter-invert {
    filter: invert(1);
}

.no-shadow {
    text-shadow: none !important;
    @include shadow(none !important);
}

.text-black {
    color: #000 !important;
}

.text-underline {
    text-decoration: underline !important;
}

.fs-overlay {
    position: fixed;
    left: 0;
    top: 0;
    z-index: 98;
    display: none;
    background-color: rgba(0,0,0,0.3);
    @include square(100%);
    &.is--active {
        display: block;
    }
}

.row--gutter-10 {
    margin-left: -10px;
    margin-right: -10px;

    *[class*='col-'] {
        padding-left: 10px;
        padding-right: 10px;
    }
}

.rounded-xl {
    @include border-radius($default-radius);
}


.opacity-1 {
    opacity: 1 !important;
}


// Media Queries
@include large() {
    .mt-0-lg {
        margin-top: 0 !important;
    }
}

@include medium() {}


@include small() {

    .bg-primary-sm {
        background: $color1 !important;
    }

    .mb-60-sm {
        margin-bottom: 3.75rem;
    }

    .mt-0-sm {
        margin-top: 0 !important;
    }
}

@include extra-small() {
    .full-width-xs {
        width: 100% !important;
    }
}
