#footer {
    font-size: 0.9375rem;
    padding: 3rem 0;
    border-top: 1px solid #d4dae0;

    @include small() {
        padding: 1.5rem 0;
    }

    .container {
        max-width: 65.875rem;
    }

    a {
        color: $text-color;
        @include transition(350ms);

        &:hover {
            color: $color1;
        }
    }

    .fa-phone-volume {
        font-size: 1.1rem;
        @include rotate(-45);
    }

    .copyright-text {
        font-size: 0.85rem;
    }

}


.footer__block-heading {
    border-bottom: 1px solid #e0e5fd;
    color: #000;
    font-size: .9rem;
    padding-bottom: .4rem;
    text-transform: uppercase;
    margin-bottom: 1rem;
}

.footer__block-list {
    li {
        margin-bottom: 1rem;
        font-weight: 500;

        &:last-child {
            margin-bottom: 0;
        }
    }


}


.footer__info-list {
    font-size: 0.9rem;
}
