.tab-navigation {
    .tab-head {
        display: flex;
        justify-content: center;
        overflow-x: auto;
        background-color: #e3e1e1;
    }

    .tab-head__item {
        cursor: pointer;
        padding: 1.275rem;
        line-height: 1;
        color: #666666;
        @include transition(350ms);

        &.is--active,
        &:hover {
            background-color: #fff;
        }
    }

}

.tab-head__item-icon {
    font-size: 1.2rem;
}

.tab-pane {
    display: none;
    padding: 2rem;
    font-size: 1.125rem;

    &.is--active {
        display: block !important;
    }

}

.tab-navigation--vertical {
    display: flex;

    .tab-head {
        display: block;
        flex: 1;
        width: 24%;

        @include medium() {
            width: 28%;
        }
    }

    .tab-content {
        flex: 3.18;
        width: 76%;

        @include medium() {
            flex: 2.5;
            width: 72%;
        }
    }

    .tab-pane {

        &.is--active {
            display: flex !important;
        }

    }
}


.tab-pane__heading {
    font-size: 1.375rem;
    margin-bottom: 1rem;
}

.tab-pane__heading--big {
    font-size: 2.65rem;
}

.tab-head-nav {
    font-size: 1.5rem;
    opacity: 0.5;
    position: absolute;
    top: 0.7rem;
    left: 0.7rem;
    &.is--active:before {
        content: '\f053';
        font-size: 1.7rem;
    }
}

.tab-head-nav, .tab-content .close {
    z-index: 2;
}
