.btn {
    font-size: 1.1rem;
    padding: 1.26rem 1.81rem;
    line-height: 1;
    font-weight: 500;
    position: relative;
    border: 1px solid transparent;
    @include shadow(-2 1px 5px 0 rgba(0,0,0,.2), 0 2px 2px 0 rgba(0,0,0,.14), 0 3px 1px -2px rgba(0,0,0,.12));
    @include border-radius($default-radius);
    @include transition(350ms ease-in-out);
    
    @include extra-small(){
        padding: 0.8rem 1.5rem;
    }

    &.disabled,
    &:disabled {
        opacity: 0.7;
    }

    &:hover,
    &:focus, &:active, &:hover:focus {
        @include shadow(0 1px 5px 0 rgba(0,0,0,.2), 0 2px 2px 0 rgba(0,0,0,.14), 0 3px 1px -2px rgba(0,0,0,.12) !important);
    }
    
    
    .fa-phone-volume {
        @include rotate(-45);
    }
}

.btn--small {
    font-size: 0.94rem;
    padding: 0.863rem 1.55rem;
}

.btn--rounded {
    @include border-radius(2rem);
}

.btn-primary {
    background-color: $color1;
    border-color: transparent !important;

    &.disabled,
    &:disabled {
        background-color: rgba($color1, 0.7);
    }

    &:hover,
    &:focus, &:active, &:hover:focus {
        background-color: darken($color1, 20%) !important;
    }
}

.btn-secondary {
    background-color: $color2;
    border-color: transparent !important;

    &.disabled,
    &:disabled {
        background-color: rgba($color2, 0.7);
    }

    &:hover,
    &:focus, &:active {
        background-color: darken($color2, 20%) !important;
    }
}

.btn-info {
    background-color: #3897f0;
    border-color: transparent !important;

    &:hover,
    &:focus, &:active {
        background-color: darken(#3897f0, 20%) !important;
    }
}

.btn-white {
    background-color: #fff !important;
    color: $color1;
    @include shadow(0 1px 5px 0 rgba(0,0,0,.2), 0 2px 2px 0 rgba(0,0,0,.14), 0 3px 1px -2px rgba(0,0,0,.12));
    
    &:hover,
    &:focus, &:active {
        color: #fff;
        background-color: $color1 !important;
    }
}

.btn-outline-primary, .btn-outline-secondary {
    @include shadow(none);
}

.btn-outline-primary {
    color: $color1;
    border: 2px solid $color1 !important;

    &:hover,
    &:focus {
        color: #fff !important;
        background-color: $color1 !important;
    }
}

.btn-outline-secondary {
    color: $color2;
    border: 1px solid $color2 !important;

    &:hover,
    &:focus {
        color: #fff !important;
        background-color: $color2 !important;
    }
}
