.menu-bar {
    cursor: pointer;
    z-index: 1000;
    position: relative;
    @include transition(350ms);
    @include large-up() {
        display: none;
    }
    .menu-bar__line {
        height: 2.4px;
        width: 22px;
        margin-bottom: 4px;
        background-color: $text-color;
        display: block;
        position: relative;
        @include transition(all 0.15s cubic-bezier(0.63, 0.62, 0.48, 1.64) 0.1s);
        @include transform(rotateZ(0deg) translateX(0px) translateY(0px));
        &:last-child {
            margin-bottom: 0;
        }
        &.menu-bar__line--second {
            @include transform(none);
        }
    }
    &.is--active {
        .menu-bar__line {
            &.menu-bar__line--second {
                opacity: 0;
            }
            &.menu-bar__line--first {
                @include transform(rotateZ(46deg) translateX(1px) translateY(1px) !important);
            }
            &.menu-bar__line--third {
                @include transform(rotateZ(-50deg) translateX(9px) translateY(-7px) !important);
            }
        }
    }
}


