.card {
    position: relative;
    border: none;
    font-size: 1rem;
    border-top: 0.625rem solid $color1;
    @include shadow(0 0 30px 3px #e6e6e6);
    @include border-radius($default-radius);

    blockquote {
        p {
            font-size: 1.2rem;
        }

        cite {
            font-style: normal;
            font-weight: 700;
            display: block;
            margin-top: 1.5rem;
        }
    }
}

.card-body {
    padding: 2.5rem 3.125rem;

    @include medium() {
        padding: 2rem 1.5rem;
    }
}

.card-title {
    font-size: 2rem;
}

.card--testimonial {
    font-size: 1.1rem;

    .card-body {
        padding: 6.25rem 4rem;

        @include medium() {
            padding: 7rem 3rem;
        }

        @include small() {
            padding: 5rem 2rem;
        }
    }

    .card__img {
        position: absolute;
        left: 4rem;
        top: -5.625rem;
        @include sphere(11.25rem);

        @include small() {
            left: 50%;
            top: -3.75rem;
            @include translate(-50%, 0);
            @include square(7.5rem);
        }

        img {
            object-fit: cover;
            @include sphere(100%);
        }
    }
}

.card--info {
    background-color: $color1;
    color: #fff;
}


.card__btns {
    .btn {
        font-weight: 700;
        font-size: 1rem;
        @include shadow(0 1px 5px 0 rgba(0, 0, 0, .2), 0 2px 2px 0 rgba(0, 0, 0, .14), 0 3px 1px -2px rgba(0, 0, 0, .12));
        @include medium-up() {
          max-width: 12.5rem;  
        }
    }
}
