.form-check {
    position: relative;
    display: inline-block;
    padding: 0;

    .form-check__label {
        display: block;
        position: relative;
        padding-left: 1rem;
        margin-bottom: 0;
        font-size: 0.8125rem;

        a {
            position: relative;
            z-index: 1;
            text-decoration: underline;
        }
        &:before {
            content: "\f0c8";
            font-family: 'Font Awesome 5 Free';
            position: absolute;
            left: 0;
            font-weight: 300;
            top: 0;
            @include transition(350ms);
        }
    }

    .form-check__input {
        opacity: 0;
        position: absolute;
        right: 0;
        z-index: 1;
        cursor: pointer;
        @include square(100%);

        &:checked {
            + .form-check__label {
                &:before {
                   content: "\f14a";
                    font-weight: 700;
                    opacity: 1;
                }
            }
        }
    }
}
