#header {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 100;
    background-color: #fff;
    border-bottom: 1px solid $color3;
    padding: 0.53rem 0;
    
    @include large() {
        padding: 1rem 0;
    }
    
    .logo img {
        max-width: 3.4375rem;
    }
    
    
    .container {
        max-width: 82rem;
    }
    
    .btn {
        font-size: 0.94rem;
        padding: 0.924rem 1.25rem;
        @include large() {
            font-size: 1.2rem;
            min-width: 200px;
        }
    }
    
}