.list {
    > li {
        margin-bottom: 1rem;
        position: relative;
        font-size: 1.1rem;
        padding-left: 1.4rem;

        &:before {
            content: "";
            position: absolute;
            left: 0;
            top: 0.5rem;
            background-color: $color1;
            @include sphere(0.48rem);
        }

        &:last-child {
            margin-bottom: 0;
        }
    }
}

.list--inline {
    display: flex;

    > li {
        margin-bottom: 0;
        margin-right: 0.6rem;
    }
}


.partners-list {
    li {
        @include extra-small() {
            width: 50%;
            margin: 1.5rem 0;
            text-align: center;
        }
    }
    img {
        -webkit-filter: grayscale(1);
        filter: grayscale(1);
        height: 2.1875rem;
        @include extra-small() {
            height: 2.5rem;
        }
    }
}
