.site-banner {
    padding: 5rem 0 2rem;
    min-height: 100vh;
    position: relative;
    background-image: linear-gradient(#fff,#f5fdf9);
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;

    @include medium() {
        min-height: 48rem;
    }

    .container {
        z-index: 1;
        position: relative;
    }

}

.site-banner__heading {
    font-size: 2.6rem;
    margin-bottom: 1rem;
    font-weight: 700;

    @include small() {
        font-size: 2rem;
    }
}

.site-banner__tagline {
    font-size: 1.2rem;

    @include small() {
        font-size: 1rem;
    }
}



.site-banner--home {
    background-image: url(../images/home-banner-bg.jpg);

    @include large-up() {
        background-attachment: fixed;
    }
    
    @include small() {
        min-height: auto;
        background: none;
    }

}
