#main-navigation {
    
    @include large() {
        padding: 3rem 0;
        position: fixed;
        right: -40rem;
        top: 0;
        max-width: 20rem;
        background-color: #fff;
        z-index: 99;
        text-align: center;
        @include square(100%);
        @include transition(350ms ease-in-out);
        @include shadow(0.5rem 0 1rem rgba(0,0,0,0.2));
    }
    
    &.is--active {
        display: block;
        right: 0;
    }
  
    .nav-list__item {
        margin-right: 2rem;
        &.active {
            .nav-list__link {
                color: #9c9c9c;
            }
        }
    }
    
    
    .nav-list__link {
        font-size: 0.94rem;
        color: $text-color;
        padding: 0.8rem 0;
        cursor: pointer;
        display: block;
        @include transition(350ms ease-in-out);
        @include large() {
            font-size: 1.2rem;
            padding: 1rem 2rem;
        }
        &:hover, &.nav-list__link--active {
            color: $color1;
        }
    }
    
}
