*,
*:after,
*:before {
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    outline: none !important;
}

::-moz-selection {
    background: rgba($color1,0.3);
    color: $color1;
    text-shadow: none;
}

::selection {
    background: rgba($color1,0.3);
    color: $color1;
    text-shadow: none;
}


html,
body {
    line-height: 1.42;
    color: $text-color;
    font-size: 16px;
    font-weight: 500;
    font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Helvetica Neue,Ubuntu,sans-serif;
    @include large() {
        font-size: 15.8px;
    }
    @include small() {
        font-size: 14px;
    }
    
    &.menu--active {
        overflow: hidden !important;
        height: 100vh;
    }
}

ul,
ol {
    margin: 0;
    padding: 0;
    list-style-type: none;
}

a {
    color: $color1;
    &:hover {
        text-decoration: none;
    }
}


img {
    vertical-align: middle;
    max-width: 100%;
    height: auto;
    -ms-interpolation-mode: bicubic;

    &.aligncenter {
        margin-right: auto;
        margin-left: auto;
        display: block;
        margin-bottom: 1rem;
    }

    &.alignleft {
        float: left;
        margin-right: 1rem;
        margin-bottom: 1rem;
    }

    &.alignright {
        float: right;
        margin-left: 1rem;
        margin-bottom: 1rem;
    }
}


p,
h1,
h2,
h3,
h4,
h5,
h6 {
    margin-top: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    line-height: 1.2;
    font-weight: 700;
}

.container {
    max-width: 77rem;
    width: 100%;
}

.container--small {
    max-width: 58rem;
}

