.section {
    padding: 5rem 0;
    font-size: 1.1rem;
    position: relative;

    @include small() {
        font-size: 1rem;
        padding: 2.5rem 0;
    }

    &.has--bg-color {
        background-color: #f5fdf9;
    }
}

.section__heading {
    font-size: 1.4rem;
    margin-bottom: 1.4rem;
}

.section__heading--small {
    font-size: 1.2rem;
    margin-bottom: 1.2rem;
}

.section__heading--big {
    font-size: 3.2rem;
    margin-bottom: 2rem;

    @include medium() {
        font-size: 2.5rem;
    }

    @include small() {
        font-size: 2.25rem;
        margin-bottom: 1rem;
    }
}

.section__heading,
.section__heading--big,
.section__heading--small {
    sub {
        font-size: 40%;
        bottom: 0.2rem;
    }
}

.letter-spacing {
    font-size: 1.0625rem;

    a {
        color: $color2;
        text-decoration: underline;
    }
}


.section__part {
    padding: 0 6rem;

    @include medium() {
        padding: 0 4rem;
    }

    @include small() {
        padding: 0 15px;
    }
}

.section__block-digit {
    font-weight: 700;
    font-size: 5rem;
    line-height: 1;
}

.section__block-heading {
    font-size: 1.4rem;
}


.section--info {
    padding: 12rem 0;

    @include medium() {
        padding: 5rem 3rem;
    }

    @include small() {
        padding: 5rem 15px;
    }

    &:after {
        content: "";
        background-color: $color3;
        position: absolute;
        right: 0;
        top: 0;
        height: 100%;
        width: 55%;
        clip-path: polygon(14% 0, 100% 0, 100% 100%, 0% 100%);
        z-index: -1;

        @include medium() {
            width: 100%;
            clip-path: none;
        }
    }

    .section__heading,
    .section__heading--big {
        font-weight: 500;

        @include small-up() {
            font-size: 4rem;
        }
    }

    p {
        font-size: 1.2rem;
    }

    li {
        font-size: 1.1rem;
    }
}
