.social-icons {
    display: flex;
    
    li {
        margin-right: 1rem;
        &:last-child {
            margin-right: 0;
        }
    }
    
    .social-icon {
        color: $text-color;
        font-size: 0.9rem;
        text-decoration: none;
        border: 1px solid $text-color;
        @extend .flex-centered;
        @include transition(350ms ease-in-out);
        @include sphere(2.125rem);
        &:hover {
            color: #fff;
            background-color: $text-color;
            @include translate(0,-0.4rem);
        }
    }
}