.selectbox {
    position: relative;
    z-index: 1;

    &:after {
        content: "";
        background: url(../images/icons/angle-down.svg) no-repeat;
        background-size: 100%;
        width: 0.8rem;
        height: 0.625rem;
        position: absolute;
        right: -1rem;
        z-index: -1;
        @include vertically-centered();
    }


    select {
        cursor: pointer;
        outline: none;
        appearance: none;
        -moz-appearance: none;
        -webkit-appearance: none;
        width: 100%;
        background: transparent !important;
        border: none;
    }
}